'use client';

import Link from 'next/link';
import { useEffect, useMemo, useState } from 'react';
import { useRouter } from 'next/navigation';
import type { Board } from '@/lib/types';

import { NavShell } from '@/app/components/NavShell';
import { Modal } from '@/app/components/Modal';
import { listInstances } from '@/lib/firebase/instances';
import { createBoard, listBoards } from '@/lib/firebase/boards';
import { useWorkspace } from '@/lib/firebase/useWorkspace';

type InstanceOption = { id: string; name?: string; gatewayUrl?: string; status?: string };

type UiBoard = Board & { id: string };

export default function BoardsPage() {
  const router = useRouter();
  const { workspaceId, authReady } = useWorkspace();
  const [boards, setBoards] = useState<UiBoard[]>([]);
  const [open, setOpen] = useState(false);

  // modal form state
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [instances, setInstances] = useState<InstanceOption[]>([]);
  const [instanceId, setInstanceId] = useState<string>('');
  const [loadingBots, setLoadingBots] = useState(false);

  useEffect(() => {
    if (!authReady) return;
    if (!workspaceId) {
      router.replace('/sign-in');
      return;
    }

    let cancelled = false;
    (async () => {
      const b = await listBoards(workspaceId);
      if (cancelled) return;
      setBoards(
        b.map((x) => ({
          id: x.id,
          name: x.name,
          description: x.description,
          instanceId: x.instanceId,
          createdAt: x.createdAt,
          updatedAt: x.updatedAt,
        }))
      );
    })().catch(() => {
      // ignore
    });

    return () => {
      cancelled = true;
    };
  }, [authReady, workspaceId, router]);

  async function openCreate() {
    if (!authReady) return;
    setOpen(true);
    setName('');
    setDescription('');
    setInstances([]);
    setInstanceId('');

    if (!workspaceId) return;

    setLoadingBots(true);
    try {
      const list = (await listInstances(workspaceId)) as InstanceOption[];
      setInstances(list);
      if (list.length === 1) setInstanceId(list[0]!.id);
    } finally {
      setLoadingBots(false);
    }
  }

  async function doCreateBoard() {
    const trimmed = name.trim();
    if (!trimmed || !workspaceId) return;

    await createBoard(workspaceId, {
      name: trimmed,
      description: description.trim() || undefined,
      instanceId: instanceId || undefined,
    });

    setOpen(false);
  }

  const items = useMemo(() => boards, [boards]);

  return (
    <NavShell title="Boards">
      <div className="flex items-center justify-end">
        <button onClick={() => void openCreate()} className="btn-primary px-4 py-2 rounded-xl font-extrabold">
          + New
        </button>
      </div>

      <div className="mt-4 grid gap-3">
        {items.length ? (
          items.map((b) => (
            <Link
              key={b.id}
              href={`/board/${encodeURIComponent(b.id)}`}
              className="panel p-4 flex items-center justify-between hover:opacity-95"
            >
              <div>
                <div className="font-extrabold">{b.name}</div>
                <div className="text-xs text-[color:var(--muted)]">{b.description || '—'}</div>
              </div>
              <div className="chip">Open</div>
            </Link>
          ))
        ) : (
          <div className="panel p-6">
            <div className="font-extrabold">No boards yet</div>
            <div className="mt-2 text-sm text-[color:var(--muted)]">
              Create a board to define scope + which OpenClaw instance runs tasks for it.
            </div>
            <button onClick={() => void openCreate()} className="mt-4 btn-primary px-4 py-3 rounded-2xl font-extrabold">
              Create your first board
            </button>
          </div>
        )}
      </div>

      <Modal open={open} title="Create board" onClose={() => setOpen(false)}>
        <div className="text-xs uppercase tracking-wide text-[color:var(--muted)] font-extrabold">Name</div>
        <input
          value={name}
          onChange={(e) => setName(e.target.value)}
          className="mt-1 w-full rounded-2xl bg-[rgba(11,12,15,.7)] border border-[color:var(--stroke)] p-3 outline-none"
          placeholder="e.g., Client sites"
        />

        <div className="mt-4 text-xs uppercase tracking-wide text-[color:var(--muted)] font-extrabold">Scope / description</div>
        <textarea
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          className="mt-1 w-full min-h-[90px] rounded-2xl bg-[rgba(11,12,15,.7)] border border-[color:var(--stroke)] p-3 outline-none"
          placeholder="What is this board for?"
        />

        <div className="mt-4 text-xs uppercase tracking-wide text-[color:var(--muted)] font-extrabold">Bot instance</div>
        <select
          value={instanceId}
          onChange={(e) => setInstanceId(e.target.value)}
          disabled={loadingBots || instances.length === 0}
          className="mt-1 w-full rounded-2xl bg-[rgba(11,12,15,.7)] border border-[color:var(--stroke)] p-3 outline-none disabled:opacity-50"
        >
          <option value="">{loadingBots ? 'Loading…' : instances.length ? 'Select a bot…' : 'No bots connected yet'}</option>
          {instances.map((i) => (
            <option key={i.id} value={i.id}>
              {i.name || i.id}
            </option>
          ))}
        </select>

        <button onClick={() => void doCreateBoard()} className="mt-4 w-full btn-primary px-4 py-3 rounded-2xl font-extrabold">
          Create board
        </button>
      </Modal>
    </NavShell>
  );
}
