import { collection, getDocs, limit, query } from 'firebase/firestore';
import { firestoreDb } from './client';

export type InstanceDoc = {
  id: string;
  name?: string;
  gatewayUrl?: string;
  status?: string;
  createdAt?: number;
  updatedAt?: number;
};

export async function listInstances(workspaceId: string): Promise<InstanceDoc[]> {
  const db = firestoreDb();
  const snap = await getDocs(collection(db, 'workspaces', workspaceId, 'instances'));
  return snap.docs.map((d) => ({ id: d.id, ...(d.data() as Omit<InstanceDoc, 'id'>) }));
}

export async function getSingleInstanceIdIfOnlyOne(workspaceId: string): Promise<string | null> {
  const db = firestoreDb();
  const q = query(collection(db, 'workspaces', workspaceId, 'instances'), limit(2));
  const snap = await getDocs(q);
  if (snap.size !== 1) return null;
  return snap.docs[0]!.id;
}
